      <div class="timer-container">
        <div class="timer-box">
          <h2>Timer</h2>
          <p class="timer-display">{{ timer }}s</p>
        </div>
      </div>

    <div v-if="showAlert" class="notification-overlay">
      <div class="notification">
        <h2>Notification</h2>
        <p>Your time is up! Try again</p>
        <button @click="restoreNormal" class="btn btn-danger">Dismiss</button>
      </div>
    </div>
  </div>
</template>

<script>
import Flash from "../components/Flash.vue";
import Flash2 from "../components/Flash2.vue";
import Cards from "@/components/Cards.vue";
import furnitureData from "@/furniture.json";

export default {
  name: "Cognitiu",
  components: {
    Cards,
    Flash,
    Flash2,
  },
  data() {
    return {
      furniture: furnitureData,
      isLarge: false, 
      isVisible: true, 
      timer: 0,
      showAlert: false,
      isDimmed: false, 
    };
  },
  mounted() {
    this.startTimer();
  },
  methods: {
    toggleSize() {
      this.isLarge = !this.isLarge;
      this.isVisible = true; 
    },
    startTimer() {
      const interval = setInterval(() => {
        this.timer++;
        if (this.timer >= 10) {
          this.showAlert = true; 
          this.isDimmed = true; 
          clearInterval(interval); 
        }
      }, 1000);
    },
    restoreNormal() {
      this.showAlert = false; 
      this.isDimmed = false; 
      this.timer = 0; 
      this.startTimer(); 
    },
  },
};
</script>

